	module.exports = {"data":{"post":{"html":"<p>We were already in upstairs in bed early to rest up for the upcoming Monday when downstairs Mama's cell phone rang. No, it can't be her phone, Mama insisted, since she doesn't have any reception at home. She believed me the second time. I guess her reception at home is only spotty and not non-existant. </p>\n<p>She went right back to sleep, but I was wide awake with a spark of inspiration for the bug database I'm adapting for work. So here I am programming when I should be sleeping. </p>\n<p>We've got nearly 10 cm of snow and it's still coming down. I'm glad I have a Christopher day tomorrow. The rush hour traffic will be a mess, with the snow and the new Elbtunnel construction that was started this weekend.</p>","fields":{"title":"Hash of hashes","date":"04 Feb 2001","url":"https://papascott.de/2001-02-04-hash-of-hashes/"}}},"pathContext":{"slug":"/2001-02-04-hash-of-hashes/","prev":{"fields":{"title":"Energy","date":"01 Feb 2001","slug":"/2001-02-01-energy/"}},"next":{"fields":{"title":"Kitchen Drawers","date":"07 Feb 2001","slug":"/2001-02-07-kitchen-drawers/"}}}}

